.Contact{
    display: flex;
    justify-content: space-around;
    align-items: center;
    text-align: center;
    height: 90vh;
    background-color: #111;
    span{
        flex-basis: 30%;
        i{
            color: #fff;
            transition: .3s;
            font-size: 9.5vw;
            p{
                font-family: 'Glory', sans-serif;
                transition: 1s;
                font-size: 2vw;
            }
        }
        i:hover{
            color: greenyellow;
        }
        i:hover p{
            color: #fff;
        }
    }
    a{
        flex-basis: 30%;
        color: #fff;
        transition: .3s;
        i{
            font-size: 9.5vw;
        }
    }
    a:hover{
        color: greenyellow;
    }
}

@media(max-width:360px){
    .Contact{
        padding-top: 50px;
        flex-direction: column;
        span{
            flex-basis: 50%;
            i{
                font-size: 15vw;
                p{
                    font-size: 5vw;
                }
            }
        }
        a{
            i{
                font-size: 15vw;
            }
        }
    }
}
