nav{
    width:100vw;
    height: 10vh;
    background-color: #111;
    font-size: 2.5vw;
    ul{
        width: 100%;
        display: flex;
        justify-content: space-around;
        line-height: 10vh;
        li {
            flex-basis: 25%;
            text-align: center;
            display: inline-block;
            a{
                color: #fff;
                text-decoration: none;
                transition: .3s;
                
            }
        }
        li:hover a{
            font-size: 1.1em;
            color: greenyellow;
        }
    }
}
@media (max-width:414px){
    nav{
        font-size: 5vw;
    }
}
