.projectWrapper{
    min-height: 90vh;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    background-color: #111;
    div{
        flex-basis: 30%;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        padding: 15px;
        h1{
            margin-bottom: 19px;
            color: #fff;
            font-size: 2vw;
        }
        img{
            width: 100%;
            border: 2px solid #fff;
            border-radius: 10px;
            margin-bottom: 10px;
            border: 2px solid #fff;
            transition: .3s;
        }
        a{
            text-decoration: none;
            padding: 5px 25px;
            border: 2px solid greenyellow;
            border-radius: 10px;
            color: #fff;
            letter-spacing: 1px;
            transition: .3s;

            &:hover{
                color: greenyellow;
            }
        }
    }
}
@media (max-width:812px){
    .projectWrapper{
        flex-direction: row;
        div{
            flex-basis: 50%;
            h1{
                font-size: 3.55vw;
            }
            img{
                width: 80%;
            }
        }
    }
}
