.AboutMeWrapper{
    width: 100vw;
    height: 90vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background: #111;
    .title{
        display: flex;
        flex-direction: column;
        align-items: center;
        color: #fff;
        h1{
            font-size: 4.5vw;
        }
        h2{
            font-size: 3.3vw;
        }
        .cursor{
            font-size: 4.5vw;
            opacity: 0;
            transition: .2s;
        }
        .cursor.active{
            opacity: 1;
        }
    }
    .Aboutme{
        display: flex;
        justify-content: space-around;
        align-items: center;
        flex-wrap: wrap;
        text-align:center;
        padding: 20px 50px;
        color: greenyellow;
        background-color: #111;
        font-size: 23px;
        font-style: bold;
        p{
            flex-basis: 40vw;
            margin-bottom: 30px;
        }
    }
}
@media(min-width: 320px){
    .AboutMeWrapper{
        .title{
            h1{
                height: 5vh;
                font-size: 9.5vw;
            }
            h2{
                height: 5vh;
                font-size: 7.5vw;
            }
        }
        .Aboutme{
            padding-top: 100px;
            p{
                flex-basis: 100%;
            }
        }
    }
}
