.skillsWrapper{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    align-items:center;
    height: 90vh;
    background-color: #111;  
    i{
        flex-basis: 25%;
        flex-grow: 1;
        text-align: center;
        font-size: 8.5vw;
        color: #fff;
        display: inline-block;
        transition: .3s;
        p{
            font-family: 'Glory', sans-serif;
            font-size: 1.5vw;
        }
    }
    i:hover{
        color: greenyellow;
    }
}